import { request } from 'utils';

const prefix = '/api/categories';

export const getListPaperType = (page = 1, IsPagingEnabled = true) =>
  request({
    prefix,
    url: '/PaperType',
    params: {
      page,
      IsPagingEnabled,
    },
    method: 'GET',
  });
export const getPaperType = id =>
  request({
    prefix,
    url: `/PaperType/GetById`,
    method: 'GET',
    params: { id },
  });
export const deletePaperType = id =>
  request({
    prefix,
    url: `/PaperType`,
    method: 'DELETE',
    data: { id },
  });
export const updatePaperType = (id, values) =>
  request({
    prefix,
    url: `/PaperType`,
    data: {
      id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      typeId: values.typeId,
    },
    method: 'PUT',
  });

export const createPaperType = values =>
  request({
    prefix,
    url: '/PaperType',
    data: {
      id: values.id,
      label: values.code,
      labelInEnglish: values.labelInEnglish || '',
      typeId: values.typeId,
    },
    method: 'POST',
  });



